import { connect } from 'react-redux'
import { requestAddresses, removeAddress } from '../actions/addresses'
import { logout } from '../actions/user'
import { IState } from '../reducers'
import Addresses, {
	IAddressesProps,
	IAddressesActionProps,
} from '../components/Addresses'

const mapStateToProps = (state: IState) => ({ addresses: state.addresses })
const mapDispatchToProps = { requestAddresses, removeAddress, logout }

export default connect<IAddressesProps, IAddressesActionProps>(
	mapStateToProps,
	mapDispatchToProps,
)(Addresses)



