import React from 'react'
import Home from '../Home'
import Login from '../../containers/LoginContainer'
import styles from './styles.scss'

export interface IAppProps {
	loggedIn: boolean
}

interface IState {
	windowAspectRatio: number
}

export default class App extends React.Component<IAppProps, IState> {
	state: IState = {
		windowAspectRatio: this.getWindowAspectRatio(),
	}

	componentDidMount() {
		window.addEventListener('resize', this.onResize)
	}

	componentWillUnmount() {
		window.removeEventListener('resize', this.onResize)
	}

	render() {
		const { loggedIn } = this.props
		const { windowAspectRatio } = this.state

		return (
			<div className={styles.wrapper}
				style={{
					'--product-image-aspect-ratio': windowAspectRatio,
				} as any}
			>
				<div className={styles.in}>
					{loggedIn ? <Home /> : <Login />}
				</div>
			</div>
		)
	}

	onResize = () => {
		this.updateWindowAspectRatio()
	}

	private updateWindowAspectRatio() {
		this.setState({ windowAspectRatio: this.getWindowAspectRatio() })
	}

	private getWindowAspectRatio() {
		return window.innerWidth / window.innerHeight * 1.1
	}

}



