import React from 'react'
import { IBasketItems } from '../../actions/basket'
import styles from './styles.sass'
import BottomBar from '../BottomBar'

export interface IBasketPreviewProps {
	items: IBasketItems
}

export interface IBasketPreviewActionProps {
	onOpenDetail: () => void
}

export default class BasketPreview extends React.Component<
	IBasketPreviewProps & IBasketPreviewActionProps
> {
	render() {
		const { items, onOpenDetail } = this.props
		const typesCount = Object.keys(items).length
		const itemsCount = Object.values(items).reduce((sum, current) => {
			return sum + current.quantity
		}, 0)

		return (
			<BottomBar isActive={typesCount > 0}>
				<div className={styles.items}>
					Distinct products: {typesCount}, bottles: {itemsCount}
				</div>
				<div className={styles.continue}>
					Proceed
					<i className="fas fa-arrow-right"></i>
				</div>
				<button
					className={styles.action}
					role="button"
					aria-label="Show basket"
					onClick={onOpenDetail}
				/>
			</BottomBar>
		)
	}
}



