import React from 'react'
import styles from './styles.sass'
import { IAddresses } from '../../actions/addresses'
import AddressEditor from '../../containers/AddressEditorContainer'
import translate from '../../utils/translate'

export interface IAddressesProps {
	addresses: IAddresses
}

export interface IAddressesActionProps {
	requestAddresses: () => void
	removeAddress: (id: string) => void
	logout: () => void
}

export default class Addresses extends React.Component<
	IAddressesProps & IAddressesActionProps
> {
	componentWillMount() {
		this.props.requestAddresses()
	}

	render() {
		const { addresses, logout } = this.props

		return (
			<section>
				<button type="button" onClick={logout}>{translate('menu.logout')}</button>
				<h1>Adresy</h1>
				<AddressEditor />
				<div className={styles.list}>
					{Object.values(addresses).map((address) => (
						<div key={address.id}>
							<h2>{address.title}</h2>
							<p>{address.address}</p>
							<button type="button">Upravit</button>
							<button type="button" onClick={this.remove(address.id)}>
								Smazat
							</button>
						</div>
					))}
				</div>
			</section>
		)
	}

	private remove = (id: string) => () => {
		this.props.removeAddress(id)
	}
}



